.licenses {
  position: relative;
  background-color: #252525;
  color: #fff;
}

.licenses__wrapper {
  padding: 15px 0;
}

.licenses__img-box {
  @include list;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: flex-start;
}

.licenses__item {
  width: 33%;
}

.licenses__content {
  padding: 15px;
  margin: 15px 0;
  background-color: #303030;
}

.licenses__title {
  position: relative;
  width: 100%;
  font-weight: 900;
  font-size: 36px;
  line-height: 42px;
  text-align: center;

  &::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 50%;
    margin-left: -76px;
    width: 152px;
    height: 5px;
    background-image: url("../img/line-yellow.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.licenses__subtitle {
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
}

.licenses__text {
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #B9B9B9;
}

.licenses__modal {
  display: none;
  position: absolute;
  top: 100px;
  left: 0;
  z-index: 2;
  margin: auto;
  max-width: 600px;

  &--show {
    display: block;
  }
}

.licenses__modal-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  background-color: transparent;
  background-image: url("../img/close.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: 0;
}

@media (min-width: 768px) {
  .licenses {
    padding: 110px 0 25px;
    margin-top: -90px;
  }

  .licenses__wrapper {
    max-width: 1170px;
    margin: auto;
    padding: 50px 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .licenses__img-box {
    width: 46%;
  }

  .licenses__content {
    width: 50%;
    margin-left: 30px;
  }
}
